import React from 'react';
import Header from "../Components/Header-NavMenu";
import '../assets/styles/App.css';
import '../assets/styles/SejaVoluntario.css';

function AvisoLegal() {
  return (
    <div className="App">
      <div className="App-header">
        <Header />
      </div>

      <div className="container">
        <h2>Aviso Legal de Proteo de Dados</h2>
        
        <div className="legal-content">
          <h3>1. Identificao do Responsvel pelo Tratamento</h3>
          <p>
            Nome da organizao: [Nome da sua organizao] <br />
            Endereo: [Endereo da organizao] <br />
            E-mail: [Email de contato]
          </p>

          <h3>2. Dados Coletados</h3>
          <p>No mbito de nosso site de voluntariado, coletamos e tratamos os seguintes dados pessoais:</p>
          <ul>
            <li>Nome completo</li>
            <li>Endereo de e-mail</li>
            <li>Nmero de telefone</li>
            <li>Profisso e especialidade</li>
            <li>Disponibilidade horria</li>
            <li>Outros dados relevantes para a gesto de voluntrios</li>
          </ul>

          <h3>3. Finalidade do Tratamento de Dados</h3>
          <p>Os dados pessoais coletados sero utilizados exclusivamente para os seguintes fins:</p>
          <ul>
            <li>Coordenao e gesto de voluntrios</li>
            <li>Atribuio de tarefas e acompanhamento de atividades</li>
            <li>Comunicao entre a organizao e os voluntrios</li>
            <li>Prestao de servios de apoio s pessoas afetadas pela inundao no Rio Grande do Sul</li>
            <li>Avaliao da eficcia das intervenes e melhoria dos servios prestados</li>
          </ul>

          <h3>4. Base Legal para o Tratamento de Dados</h3>
          <p>O tratamento dos dados pessoais  baseado no consentimento explcito dos interessados, que fornecem seus dados de forma voluntria atravs do formulrio disponvel em nosso site.</p>

          <h3>5. Direitos dos Titulares dos Dados</h3>
          <p>Os titulares dos dados tm o direito de:</p>
          <ul>
            <li>Acessar seus dados pessoais</li>
            <li>Solicitar a correo de dados inexatos</li>
            <li>Solicitar a eliminao de seus dados pessoais</li>
            <li>Opor-se ao tratamento de seus dados pessoais</li>
            <li>Solicitar a limitao do tratamento de seus dados pessoais</li>
            <li>Portabilidade dos dados</li>
          </ul>

          <h3>6. Segurana dos Dados</h3>
          <p>Implementamos medidas tcnicas e organizacionais apropriadas para proteger os dados pessoais contra perda, uso indevido, acesso no autorizado, divulgao, alterao e destruio.</p>

          <h3>7. Alteraes ao Aviso Legal</h3>
          <p>Reservamo-nos o direito de atualizar este Aviso Legal de Proteo de Dados periodicamente. Recomendamos que os voluntrios revisem este aviso regularmente para se manterem informados sobre como protegemos seus dados.</p>
        </div>
      </div>

      <footer className="App-footer"></footer>
    </div>
  );
}

export default AvisoLegal;
