import React from "react";
import Header from "../Components/Header-NavMenu";
import '../assets/styles/App.css';
import '../assets/styles/HomePage.css'
import psicologa from '../assets/images/psicologa.jpg'; 
import educador from "../assets/images/educador-social.jpg";
import lideres from "../assets/images/lideres.png";
import voluntario from '../assets/images/voluntariado 1.jpeg';
 
function Principal() {
  return (
    <div className="App">
      <header className="App-header">
        <Header/>
      </header>
      <body>
        <div className="background-main">
          <div className='main'>
            <div className='text-home'>
              <div className='titulo-home'>
                <h2>TOTERS do BEM</h2>
                <h1> SOS rio grande do sul</h1>
              </div>
              <h3>nosso objetivo  encaminh-lo  ajuda emocional gratuita e online</h3>  
                <div className='sub-text-home'>
                  <a href="./demo">Liderana Emigrante </a>
                  <a href='./JoinUs'>Quero ser voluntrio</a>
                </div>
                </div>
            </div>
          </div>
          <div className="voluntariado">
          <div className='tipo-vo-texto'>
              <h1>Como voc pode ajudar?</h1>
              <p>sua ajuda  crucial para fornecer apoio emergencial s comunidades afetadas, h vrias maneiras pelas quais voc pode contribuir:</p>
            </div>
            <div className="voluntariado-img"></div>
          </div>
          <div className="accordion">
      <div className="accordion-container">
        <div className="accordion-item">
          <div className="accordion-content">
            <h2>Voluntariado</h2>
            <img src={psicologa} alt="Voluntariado Psicolgico" />
            <h2>Psicolgico</h2>
          </div>
        </div>
        <div className="accordion-item">
          <div className="accordion-content">
            <h2>Educadores</h2>
            <img src={educador} alt="Educadores Sociais" />
            <h2>Sociais</h2>
          </div>
        </div>
        <div className="accordion-item">
          <div className="accordion-content">
            <h2>Lderes</h2>
            <img src={lideres} alt="Lderes Comunitrios" />
            <h2>Comunitrios</h2>
          </div>
        </div>
      </div>
    </div>
    <div className="proceso-voluntariado">
      <h1>processo de voluntariado</h1>
      <div className="proceso-texto">
        <h2>registro</h2>
        <p>complete o <a href="./JoinUs">formulrio</a> de inscrio com seus dados pessoais e experincia.</p>
      </div>
      <div className="proceso-texto"> 
        <h2>capacitao</h2>
        <p>participe de uma breve sesso de capacitao onde forneceremos todas as informaes necessrias para sua funo.</p>
      </div>
      <div className="proceso-texto"> 
        <h2>atribuio</h2>
        <p>voc ser designado para uma rea especfica com base em suas habilidades e disponibilidade trs dias aps o envio do formulrio.</p>
      </div>
      <div className="proceso-texto"> 
        <h2>ao</h2>
        <p>comece a oferecer seu apoio s comunidades afetadas, fazendo uma diferena real e positiva.</p>
      </div>
      

    </div>
      </body>
    </div>
  );
}

export default Principal;
