import React, { useState } from "react";
import { Link } from "react-router-dom";
import InputMask from "react-input-mask";
import Header from "../Components/Header-NavMenu";
import '../assets/styles/App.css';
import '../assets/styles/SejaVoluntario.css';

// aca se importo la libreria InputMask para hacer la validacin de los datos en el cpf y whatsapp 
// falta una la informacin del login, usuario y senha, se podr confirmar el email?

function Principal() {
  const [voluntario, setVoluntario] = useState("");
  const [isTermsAccepted, setIsTermsAccepted] = useState(false);
  const [phone, setPhone] = useState("");
  const [cpf, setCpf] = useState("");

  const handleVoluntarioChange = (event) => {
    setVoluntario(event.target.value);
  };

  const handleTermsChange = (event) => {
    setIsTermsAccepted(event.target.checked);
  };

  const handlePhoneChange = (event) => {
    const input = event.target.value;
    setPhone(input);
  };

  const handleCpfChange = (event) => {
    const input = event.target.value;
    setCpf(input);
  };
  const handleSubmit = (event) => {
    if (!isTermsAccepted) {
      event.preventDefault();
      alert("Por favor, aceite os termos e condies antes de enviar o formulrio.");
    }
  };
  return (
    <div className="App SV">
      <div className="App-header">
        <Header />
      </div>
      <div className="container">
        <h2>SOS Rio Grande do Sul - Cadastro de profissionais de sade e intrpretes voluntrios</h2>
        <form className="inputs" onSubmit={handleSubmit}>
          <div className="input-field">
            <h4>1. Nome Completo<span>*</span></h4>
            <input type="text" placeholder="Digite seu nome" required />
          </div>
          <div className="input-field">
            <h4>2. CPF<span>*</span></h4>
            <InputMask
              mask="999.999.999-99"
              value={cpf}
              onChange={handleCpfChange}
              placeholder="Digite seu CPF"
              required  className="input-text"
            />
            <input class="input-text" type="text" placeholder="O valor deve ser numrico" required />
          </div>

          <div className="input-field">
            <h4>3. Data de Nascimento<span>*</span></h4>
            <input type="date" required />
          </div>

          <div className="input-field">
            <h4>4. Nmero do WhatsApp<span>*</span></h4>

            <InputMask
              mask="(99) 99999-9999"
              value={phone}
              onChange={handlePhoneChange}
              placeholder="(DDD) Digite o nmero"
              required  className="input-text"/>

            <input class="input-text" type="text" placeholder="(DDD) Digite o nmero" required />

          </div>

          <div className="input-field">
            <h4>5. E-mail<span>*</span></h4>
            <input type="email" placeholder="Digite o e-mail" required />
          </div>

          <div className="form-group">
            <h4>6. Tipo de voluntrio<span>*</span></h4>
            <select className="form-select" name="voluntario" onChange={handleVoluntarioChange} required>
              <option value="">Selecione</option>
              <option value="Educador social">Educador(a) Social</option>
              <option value="Psiclogo">Psiclogo(a)</option>
              <option value="Liderana Para Emigrantes, refugiados e aptridas">Liderana Para Emigrantes, refugiados e aptridas</option>
            </select>
          </div>

          {voluntario === "Psiclogo" && (
            <>
              <div className="input-field">
                <h4>CRM<span>*</span></h4>
                <input type="text" placeholder="Digite seu CRM" required />
              </div>
              <div className="input-field">
                <h4>Especialidade<span>*</span></h4>
                <input type="text" placeholder="Digite sua especialidade" required />
              </div>
            </>
          )}

          {(voluntario === "Educador social" || voluntario === "Liderana Para Emigrantes, refugiados e aptridas") && (
            <div className="input-field">
              <h4>rea em que pode ajudar<span>*</span></h4>
              <input type="text" placeholder="Digite a rea em que pode ajudar" required />
            </div>
          )}

          <div className="form-group">
            <h4>7. Estado <span>*</span></h4>
            <select className="form-select" name="estado" required>
              <option value="">Selecione</option>
              <option value="SP">SP</option>
              <option value="MT">MT</option>
              <option value="MG">MG</option>
              <option value="CE">CE</option>
              <option value="AC">AC</option>
              <option value="AM">AM</option>
              <option value="RS">RS</option>
              <option value="MA">MA</option>
              <option value="MS">MS</option>
              <option value="RJ">RJ</option>
              <option value="RO">RO</option>
              <option value="SC">SC</option>
            </select>
          </div>

          <div className="form-group">
            <h4>8. Disponibilidade<span>*</span></h4>
            <select className="form-select" name="disponibilidade" required>
              <option value="">Selecione</option>
              <option value="Manh">Manh</option>
              <option value="Tarde">Tarde</option>
              <option value="Noite">Noite</option>
            </select>
          </div>

          <div>
            <h4>Observao (opcional)</h4>
            <textarea name="message" cols="60" rows="10" placeholder="Sua mensagem" className="contact-inputs"></textarea>
          </div>

          <div className="form-group">
            <input type="checkbox" id="terms" name="terms" onChange={handleTermsChange} required />
            <label htmlFor="terms">
              Ao marcar esta caixa e clicar em Enviar, aceito o tratamento de meus dados pessoais por <Link to="/avisoLegal" target="_blank">[Nome da sua organizao]</Link> conforme explicado no seu <Link to="/avisoLegal" target="_blank">Aviso Legal de Proteo de Dados</Link>, que inclui: 1) a coordenao e gesto de voluntrios, e 2) a comunicao sobre atividades e oportunidades relacionadas.
            </label>
          </div>

          <button class="SV" type="submit">Enviar</button>
        </form>
      </div>

      <footer className="App-footer"></footer>
    </div>
  );
}

export default Principal;